<template>
  <div id="auth-main">
    <div id="auth-image">
      <img
        src="../assets/images/auth/auth-image.svg"
        alt="melytix"
        draggable="false"
      />
      <div id="auth-logo">
        <img
          src="../assets/images/auth/logo.svg"
          alt="logo"
          draggable="false"
        />
      </div>
    </div>
    <div id="auth-form">
      <Signup v-if="isSignup" />
      <Login v-if="!isSignup" />
      <SocialBtns />
      <div v-if="isSignup">
        <span class="text-have-account" @click="haveAccount"
          >Already have account</span
        >
      </div>
    </div>
    <div v-if="isMessage" class="auth-message" @click="closeMessage">
      <span>{{ message }}</span>
    </div>
  </div>
</template>
<script>
import Signup from "@/components/auth/Signup";
import Login from "@/components/auth/Login";
import SocialBtns from "@/components/auth/SocialBtns";
import { mapState, mapMutations } from "vuex";

export default {
  components: {
    Login,
    Signup,
    SocialBtns
  },
  computed: {
    ...mapState("auth", ["isSignup", "isMessage", "message"])
  },
  methods: {
    ...mapMutations("auth", ["SET_ISSIGNUP", "DEL_AUTHMESSAGE"]),
    haveAccount() {
      this.SET_ISSIGNUP();
    },
    closeMessage() {
      this.DEL_AUTHMESSAGE();
    }
  }
};
</script>
