<template>
  <div id="social-auth-container">
    <div id="social-auth-h2">
      <span>OR</span>
    </div>
    <div v-google-signin-button="clientId" id="google-signin-btn">
      <span>Login with Google</span>
      <img
        src="@/assets/images/auth/google_auth.png"
        alt="google-auth"
        draggable="false"
      />
    </div>
    <div id="facebook-auth-btn" @click="login">
      <span>Login with Facebook</span>
      <img
        src="@/assets/images/auth/facebook_btn.svg"
        alt="facebook-auth"
        draggable="false"
      />
    </div>
  </div>
</template>
<script>
import { mapMutations } from "vuex";
import GoogleSignInButton from "@/services/GoogleButton";
export default {
  directives: {
    GoogleSignInButton
  },
  data() {
    return {
      token: "dhjsakldhfewiohfuewihfkjshfuweiohfu",
      clientId:
        "380470694344-0a4vb8rvio43bje2dmbs5hk7l8ecdglm.apps.googleusercontent.com"
    };
  },
  methods: {
    ...mapMutations("auth", ["SOCIAL_LOGIN"]),
    login() {
      this.SOCIAL_LOGIN(this.token);
      this.$router.push({ name: "DashLayout" });
    },
    OnGoogleAuthSuccess(idToken) {
      this.SOCIAL_LOGIN({ token: idToken });
      this.$router.push({ name: "DashLayout" });
    },
    OnGoogleAuthFail({ error }) {
      this.SOCIAL_LOGIN({ Error: error });
    }
  }
};
</script>
